@import url('https://fonts.googleapis.com/css2?family=Creepster&family=Georama:ital,wght@1,200&family=Josefin+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Creepster&display=swap');

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

body {
  background: linear-gradient(#000000b0,
      #000000b0), url(./img/ZombiePattern.jpg) repeat;
}

.container {
  width: 100%;
  height: 100%;
  padding-top: 70px;
}

.content {
  max-width: 1250px;
  min-height: 90vh;
  margin: auto;
  background: rgba(0, 0, 0, 0.45);
  box-shadow: 0 8px 32px 0 rgba(128, 135, 31, 0.37);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border-radius: 10px;
  border: 1px solid rgba(166, 212, 56, 0.767);
  padding: 50px 20px;
}

@media (max-width: 735px) {
  .container {
    padding-top: 65px;
  }

  .content {
    padding: 15px 15px 35px 15px;
  }
}

@media (max-width: 550px) {
  .container {
    padding-top: 45px;
  }

  .content {
    padding: 10px 0 25px 0;
  }
}